<template>
  <div class="article-page">
    <nav class="my-nav van-hairline--bottom">
      <a
        :class="{active: sorter === 'weight_desc'}"
        @click="changeSorter('weight_desc')"
        href="javascript:;"
        >推荐</a
      >
      <a
        :class="{active: sorter === null}"
        @click="changeSorter(null)"
        href="javascript:;"
        >最新</a
      >
      <div class="logo"><img src="@/assets/logo.png" alt=""></div>
    </nav>
    <van-list v-model="loading" :finished="finished" @load="onLoad" finished-text="没有更多数据了">
      <ArticleItem v-for="item in list" :key="item.id" :item="item"></ArticleItem>
    </van-list>
  </div>
</template>

<script>
import { getArticle } from '@/api/article'
export default {
  name: 'ArticlePage',
  data () {
    return {
      current: 1,
      sorter: 'weight_desc',
      list: [],
      loading: false,
      finished: false
    }
  },
  created () {
    // const res = await getArticle({
    //   current: this.current,
    //   sorter: this.sorter
    // })
    // console.log(res.data)
    // this.list = res.data.rows
  },
  methods: {
    async onLoad () {
      const res = await getArticle({
        current: this.current,
        sorter: this.sorter
      })
      console.log(res)
      // 需要在this.list 基础上，累加 res.data.rows
      this.list.push(...res.data.rows)
      // 如果数据已经请求完毕，需要将loading改为false,才能加载下一页的数据
      // 一旦 loading 改为 false, load事件可以再次触发
      this.loading = false
      this.current++ // 当前页+1

      // 对于没有更多数据的数据
      if (this.current > res.data.pageTotal) {
        this.finished = true
      }
    },
    changeSorter (value) {
      // 修改排序规则（推荐/最新）
      this.sorter = value

      // 重置数据
      this.current = 1 // 排序条件变化，重新从第一页开始加载数据
      this.list = [] // 数据重置为空
      this.finished = false // finished 重置，重新有数据可以加载了

      // 标记需要开始加载了，由于我们是手动调用加载更多 onLoad方法
      // 所以loading 需要自己改成true,避免重复触发
      this.loading = true

      // 根据最新的条件重新渲染
      this.onLoad()
    }
  }
}
</script>

<style lang="less" scoped>
.article-page {
  margin-bottom: 50px;
  margin-top: 44px;
  .my-nav {
    height: 44px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 999;
    background: #fff;
    display: flex;
    align-items: center;
    > a {
      color: #999;
      font-size: 14px;
      line-height: 44px;
      margin-left: 20px;
      position: relative;
      transition: all 0.3s;
      &::after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        width: 0;
        height: 2px;
        background: #222;
        transition: all 0.3s;
      }
      &.active {
        color: #222;
        &::after {
          width: 14px;
        }
      }
    }
    .logo {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      > img {
        width: 64px;
        height: 28px;
        display: block;
        margin-right: 10px;
      }
    }
  }
}
</style>
